<template>
  <div class="login-container">
    <div class="login-box">
      <h2 class="login-title">欢迎来到Youon组件库</h2>
      
      <form @submit.prevent="handleLogin" class="login-form">
        <!-- 账号输入 -->
        <div class="input-group">
          <label class="input-icon">
            <!-- <i class="fas fa-user"></i> -->
            <input 
              type="text" 
              v-model="form.username" 
              placeholder="请输入账号"
              @focus="isFocused = true"
              @blur="isFocused = false"
            >
          </label>
        </div>

        <!-- 密码输入 -->
        <div class="input-group">
          <label class="input-icon">
            <!-- <i class="fas fa-lock"></i> -->
            <input 
              :type="showPassword ? 'text' : 'password'"
              v-model="form.password" 
              placeholder="请输入密码"
              @focus="isFocused = true"
              @blur="isFocused = false"
            >
            <span 
              class="password-toggle"
              @click="showPassword = !showPassword"
            >
              <img src="../assets/loginicon/openeyes.png" v-if="showPassword" alt="">
              <img src="../assets/loginicon/closeeyes.png" v-else alt="">

            </span>
          </label>
        </div>

        <!-- 记住我 & 忘记密码 -->
        <div class="form-footer">
          <label class="remember-me">
            <input type="checkbox" v-model="rememberMe">
            记住我
          </label>
          <div class="">
            <router-link to="/login/login" class="register-link">注册账号</router-link>
            <a href="javascript:;" class="forgot-password">忘记密码?</a>
          </div>
        </div>

        <!-- 登录按钮 -->
        <button 
          type="submit"
          class="login-btn"
          :class="{ 'active': isFormValid }"
          :disabled="!isFormValid"
          @click.prevent="login"
        >
          <span v-if="!isLoading">登录</span>
          <span v-else class="loading-spinner"></span>
        </button>

      </form>

      <!-- 验证码登录 -->
      <button class="login-with-captcha" @click="loginWithCaptcha">
        验证码登录
      </button>
      <!-- 第三方分割线 -->
      <div class="other-login-line">
        <div class="line"></div>
        <span>其他登录方式</span>
        <div class="line"></div>
      </div>
      <!-- 第三方登录 -->
      <div class="ohter-login">
        <img src="../assets/loginicon/weixin.png" alt="">
      </div>
      <!-- 底部信息 -->
      <div class="login-footer">点击继续 代表您同意 <a href="javascript:;" class="terms-of-service">用户协议</a> 和 <a href="javascript:;" class="terms-of-service">隐私协议</a></div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      form: {
        username: '',
        password: ''
      },
      showPassword: false,
      rememberMe: false,
      isFocused: false,
      isLoading: false
    }
  },
  computed: {
    isFormValid() {
      return this.form.username.trim() !== '' && this.form.password.length >= 6
    }
  },
  methods: {
    loginWithCaptcha(){
      console.log('暂无验证码登录功能');
      this.$router.push('/');
    },
    async handleLogin() {
      if (!this.isFormValid) return
      
      try {
        this.isLoading = true
        // 这里添加实际登录逻辑
        // 示例：await this.$http.post('/api/login', this.form)
        
        // 模拟登录成功
        setTimeout(() => {
          this.$router.push('/dashboard')
        }, 1000)
      } catch (error) {
        console.error('登录失败:', error)
        // 这里添加错误处理
      } finally {
        this.isLoading = false
      }
    }
  },
  
}
</script>

<style scoped>
/* 针对 Chrome/Safari/Edge 的方案 */
input::-webkit-credentials-auto-fill-button,
input::-webkit-password-toggle-button {
  display: none !important;
  visibility: hidden;
}
 
/* 针对 Firefox 的方案 */
input::-ms-reveal {
  display: none !important;
}
 
/* 通用方案（部分浏览器可能不生效） */
input[type="password"] {
  -webkit-text-security: disc !important;
}

.login-container {
  min-height: 100vh;
  background: url('@/assets/bgimages/loginBackground.jpg') no-repeat center center;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
}

.login-box {
  background: rgba(255, 255, 255, 0.3);
  backdrop-filter: blur(10px);

  padding: 30px 65px 0 65px;
  border: 1px solid #ccc;
  border-radius: 16px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  width: 100%;
  max-width: 450px;
  transition: all 0.3s ease;
}

.login-title {
  text-align: center;
  margin-bottom: 20px;
  color: #2c3e50;
  font-size: 28px;
  color: #000;
}

.input-group {
  position: relative;
  margin-bottom: 25px;
}

.input-icon {
  position: relative;
  display: block;
  width: 100%;
}


.input-icon input {
  width: 100%;
  height: 50px;
  padding: 0 10px;
  border: 2px solid #e4e7ed;
  border-radius: 8px;
  font-size: 16px;
  transition: all 0.3s;
  background: #f8f9fa;
}

.input-icon input:focus {
  border-color: #667eea;
  box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1);
  outline: none;
}

.input-icon input:focus + i {
  color: #667eea;
}

.password-toggle {
  position: absolute;
  right: 15px;
  top: 50%;
  transform: translateY(-50%);
  cursor: pointer;
  color: #909399;
}

.form-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 30px;
}

.remember-me {
  display: flex;
  align-items: center;
  gap: 8px;
  color: #606266;
  font-size: 14px;
}

.forgot-password {
  color: #667eea;
  font-size: 14px;
  text-decoration: none;
  
}

.forgot-password:hover {
  text-decoration: underline;
}

.register-link{
  color: #ff6b6b;
  margin-left: 5px;
  font-size: 14px;
  text-decoration: none;
  margin-right: 20px;
}
.register-link:hover {
  text-decoration: underline;
}

.login-btn {
  width: 100%;
  height: 50px;
  background: #a0a4a8;
  border: none;
  border-radius: 8px;
  color: white;
  font-size: 16px;
  /* cursor: not-allowed; */
  transition: all 0.3s;
  position: relative;
  overflow: hidden;
}

.login-btn.active {
  background: #667eea;
  cursor: pointer;
}

.login-btn.active:hover {
  background: #5a67d8;
  transform: translateY(-1px);
  box-shadow: 0 5px 15px rgba(102, 126, 234, 0.3);
}

.loading-spinner {
  display: inline-block;
  width: 20px;
    height: 20px;
    border: 3px solid rgba(255,255,255,.3);
    border-radius: 50%;
    border-top-color: #fff;
    animation: spin 1s ease-in-out infinite;
}

@keyframes spin {
  to { transform: rotate(360deg); }
}

/* 响应式设计 */
@media (max-width: 480px) {
  .login-box {
    padding: 25px;
    margin: 20px;
  }
  
  .login-title {
    font-size: 24px;
    margin-bottom: 30px;
  }
}

/* 第三方登录 */
.login-with-captcha{
  width: 100%;
  height: 50px;
  background: #fff;
  border: 1px solid #ccc;
  border-radius: 8px;
  font-size: 16px;
  transition: all 0.3s;
  cursor: pointer;
  overflow: hidden;
  margin: 20px 0;
  color:#409eff;
}
.login-with-captcha:hover {
  background: rgb(247, 247, 247);
}

.other-login-line{
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 20px 0;
}
.other-login-line span{
  flex: 1;
  color: #a1a1a1;
}
.line{
  flex: 1;
  border:1px solid #a8a8a8;
}

.ohter-login{
  width: 100%;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 16px;
  margin-bottom: 30px;
}
.ohter-login img{
  width: 40px;
  height: 40px;
  cursor: pointer;
  /* background: #fff; */
}
.login-footer{
  font-size: 12px;  
  margin: 12px 0;
}

.terms-of-service{
  color: #525252;
  cursor: pointer;
}
.terms-of-service:hover{
  color: #409eff;
}
</style>